import React from 'react'
import {withRouter } from 'react-router-dom';
import Chart from 'chart.js';
import {pieceLabel} from 'chart.piecelabel.js';

import '../App.css'; 

class Skills extends React.Component {
    
    constructor(props){
        super(props);
        
    }
    
    componentDidMount() {
        this.initializeChart1();
        this.initializeChart2();
        this.initializeChart3();
        this.initializeChart4();
        this.initializeChart5();
    }
    
    initializeChart1() {
        let el = document.getElementById("chart1");
        let ctx = el.getContext("2d");
        let chart = new Chart(ctx, {
            type: 'doughnut',
            
            data: {
            labels: ["ReactJS","NodeJS","HTML","CSS"],
                datasets: [{
                backgroundColor: [
                "rgb(154,144,245)",
                "rgb(245,202,157)",
                "rgb(222,127,141)",
                "rgb(180,212,152)"
                ],
            data: ["30","30","20","20"]
    }]
            },
            options : {
                legend : {display:false},
                pieceLabel: {
                    render: 'label',
                    overlap:true,
                    fontSize: 14,
                    fontStyle: 'bold',
                    fontColor: '#000',
                    }
            }
        });
    }
    initializeChart2() {
        let el = document.getElementById("chart2");
        let ctx = el.getContext("2d");
        let chart = new Chart(ctx, {
            type: 'doughnut',
            data: {
            labels: ["C++","C","Java","Python"],
                datasets: [{
                backgroundColor: [
                "rgb(154,144,245)",
                "rgb(245,202,157)",
                "rgb(222,127,141)",
                "rgb(180,212,152)"
                ],
            data: ["50","30","10","10"]
    }]
            },
            options : {
                legend : {display:false},
                pieceLabel: {
                    render: 'label',
                    fontSize: 14,
                    fontStyle: 'bold',
                    fontColor: '#000',
                    overlap:true
                    
                    }
            }
        });
    }
    
    initializeChart3() {
        let el = document.getElementById("chart3");
        let ctx = el.getContext("2d");
        let chart = new Chart(ctx, {
            type: 'doughnut',
            data: {
            labels: ["Ecoute","Respect","Bienveillance"],
                datasets: [{
                backgroundColor: [
                "rgb(154,144,245)",
                "rgb(245,202,157)",
                "rgb(222,127,141)"
                ],
            data: ["33","33","33"]
    }]
            },
            options : {
                legend : {display:false},
                pieceLabel: {
                    render: 'label',
                    fontSize: 14,
                    fontStyle: 'bold',
                    fontColor: '#000',
                    overlap:true
                    
                    }
            }
        });
    }
    
    initializeChart4() {
        let el = document.getElementById("chart4");
        let ctx = el.getContext("2d");
        let chart = new Chart(ctx, {
            type: 'doughnut',
            data: {
            labels: ["Analyse d'images","Tracking","Reconnatre les formes","Segmentation"],
                datasets: [{
                backgroundColor: [
                "rgb(154,144,245)",
                "rgb(245,202,157)",
                "rgb(222,127,141)",
                "rgb(180,212,152)"
                ],
            data: ["40","20","20","30"]
    }]
            },
            options : {
                legend : {display:false},
                pieceLabel: {
                    render: 'label',
                    fontSize: 14,
                    fontStyle: 'bold',
                    fontColor: '#000',
                    overlap:true
                    
                    }
            }
        });
    }
    
    initializeChart5() {
        let el = document.getElementById("chart5");
        let ctx = el.getContext("2d");
        let chart = new Chart(ctx, {
            type: 'doughnut',
            data: {
            labels: ["Franais Natif","Anglais C1","Espagnol B1"],
                datasets: [{
                backgroundColor: [
                "rgb(154,144,245)",
                "rgb(245,202,157)",
                "rgb(222,127,141)"
                ],
            data: ["60","35","5"]
    }]
            },
            options : {
                legend : {display:false},
                pieceLabel: {
                    render: 'label',
                    fontSize: 14,
                    fontStyle: 'bold',
                    fontColor: '#000',
                    overlap:true
                    
                    }
            }
        });
    }
    
    
    
    
    render() {
        return(
            <div>
                <div id='Skills'>
                    <div className='section-div'>
                        <h2>Comptences</h2>
                    </div>
                    
                    
                    <div style={{"display":"flex","align-items":"center"}}>
                        <div style={{"width":"20%"}}>
                            <h3> Programmation web </h3>
                            <canvas id="chart1" style={{"height":"250px"}} />
                        </div>

                        <div style={{"width":"20%"}}>
                            <h3> Langages de programmation </h3>
                            <canvas id="chart2" style={{"height":"250px"}} />
                        </div>
                        <div style={{"width":"20%"}}>
                            <h3> Humaines </h3>
                            <canvas id="chart3" style={{"height":"250px"}} />
                        </div>
                        <div style={{"width":"20%"}}>
                            <h3> Traitement d'images </h3>
                            <canvas id="chart4" style={{"height":"250px"}} />
                        </div>
                        <div style={{"width":"20%"}}>
                            <h3> Langues </h3>
                            <canvas id="chart5" style={{"height":"250px"}} />
                        </div>
                    </div>
                    <h3>Logiciels</h3>
                    
                </div>
            </div>
            )
        
    }
        
}


export default Skills;


