import React from 'react'
import {withRouter } from 'react-router-dom';
import enib from '../img/enib.png';
import stavanger from '../img/stavanger.png';
import bac from '../img/bac.jpg';

import '../App.css'; 

class Education extends React.Component {
    
    constructor(props){
        super(props);
        
    }
        
    render() {
        return(
            
                <div id='Education'>
                    <div className='section-div'>
                        <h2 >Formation</h2>
                    </div>
                        
                   
                    <div style={{"margin-top":"32px","width":"1000px","vertical-align":'top',"marginLeft":"20%"}}>
                            <div className='component-experience'> 
                                <img  className="logo"  style={{"width":'111px',"height":'100px'}} src={stavanger}/>
                                <div style={{"margin-left":'20px'}}>
                                    
                                    <p className='name-institution'> University of Stavanger</p>
                                    <p className="date">2018  Stavanger (Norvge) </p> 
                                    <p className="text-component" >
                                        Projet informatique (mthode AGILE)  Management de projet  Vision par ordinateur  Data-Science
                                    </p>
                                </div>
                            </div>

                        <div className='component-experience2'> 
                                <img  className="logo" style={{"width":'100px',"height":'100px'}} src={enib}/>
                                <div style={{"margin-left":'20px'}}>
                                    
                                    <p className='name-institution'>Ecole Nationale d'Ingnieurs de Brest</p>
                                    <p className="date">2016-2018  Brest </p>
                                    <p className="text-component">Deuxime anne de cycle ingnieur. <br/> <span className='description-enib'>(Ecole gnraliste  Bac+5. Option en 4me anne en informatique, lectronique ou mcatronique)</span> <br/>
                                    Informatique  Traitement et Analyse d'images  Electronique  Mcanique</p> 

                                </div>
                            </div> 

                            <div className='component-experience'> 
                                <img  className="logo" style={{"width":'100px',"height":'100px'}} src={enib}/>
                                <div style={{"margin-left":'20px'}}>
                                    
                                    <p className='name-institution'>Ecole Nationale d'Ingnieurs de Brest</p>
                                    <p className="date">2014-2016  Brest </p>
                                    <p className="text-component">Cycle prparatoire<br/></p> 

                                </div>
                            </div>

                            <div className='component-experience2'> 
                                <img  className="logo" style={{"width":'100px',"height":'100px'}} src={bac}/>
                                <div style={{"margin-left":'20px'}}>
    
                                    <p className='name-institution'>Lyce Amiral Ronarc'h</p>
                                    <p className="date">2014  Brest </p>
                                    <p className="text-component">Obtention du baccalaurat scientifique mention bien<br/></p> 

                                </div>
                            </div>
                         
                    </div>
                </div>
            
            )
        
    }
        
}


export default Education;


